import React, { Component } from 'react';
import './App.css';

import { Route } from 'react-router-dom'

import Header from './Header'
import Footer from './Footer'
import Home from './Home'
import Contact from './Contact'
import Shoppers from './Shoppers'
import Interested from './Interested'
// {process.env.PUBLIC_URL + '/assets/images/topBg.jpg'}
// publicUrl = 
// import banner from {process.env.PUBLIC_URL + '/images/banner.png'}

class App extends Component {
  constructor(props) {
    super(props)

    let theUrl = window.location.href
    theUrl = theUrl.split('/')
    theUrl = theUrl[theUrl.length - 1]

    this.state={
      links: [
        {
          displayText: 'Home',
          active: ((theUrl !== 'forshoppers') && (theUrl !== 'interested')),
          link: '/'
        },
        {
          displayText: 'For Shoppers',
          active: (theUrl === 'forshoppers'),
          link: '/forshoppers'
        },
        {
          displayText: 'Interested in Piphany?',
          active: (theUrl === 'interested'),
          link: '/interested'},
      ]      
    }
  }

  clickLink(index) {
    let links = this.state.links

    links = links.map((link, i) => {
      link.active = (i === index)
      return link
    })

    this.setState({links: links})
  }

  render() {
    return (
      <div className="App">
        <Header links={this.state.links} onClick={this.clickLink.bind(this)} />
        <div className='container'>
          <img className='banner' alt='Shop Em and Stacy Boutique' src={process.env.PUBLIC_URL + '/images/banner.png'} />
          <Route exact path='/' render={(props) => (
            <Home />
          )} />
          <Route exact path='/contact' render={(props) => (
            <Contact />
          )} />
          <Route exact path='/forshoppers' render={(props) => (
            <Shoppers />
          )} />
          <Route exact path='/interested' render={(props) => (
            <Interested />
          )} />
        </div>
        <Footer />
      </div>
    );
  }
}

export default App;



