import React, { Component } from 'react'

// import forShoppers from '../images/how_to_shop.png'
// import returnPolicy from '../images/return_policy.png'
// import './home.css'

class Shoppers extends Component {
	render() {
		return (
			<div className='row'>
				<img src={process.env.PUBLIC_URL + '/images/how_to_shop.png'} alt='how to shop' className='ten columns offset-by-one big-picture' />
				<img src={process.env.PUBLIC_URL + '/images/return_policy.png'} alt='return policy' className='ten columns offset-by-one big-picture' />
			</div>
		)
	}
}

export default Shoppers


