import React, { Component } from 'react'
import './header.css'

import { Link } from 'react-router-dom'

class Header extends Component {

	render() {
		const links = this.props.links.map((link, i) => (
			<li key={link.displayText} className={link.active ? 'active' : ''} onClick={() => this.props.onClick(i)}>
				<Link to={link.link}>
					{link.displayText}
				</Link>
			</li>
		))

		return (
			<div id='cssmenu'>
				<ul>
				   {links}
				</ul>
			</div>
		)
	}
}

export default Header



